  width: 100px;
  color: #fff;
  cursor: pointer;
  background-color: #4c4ab4;
  margin-bottom: 20px;
  padding: 3px;
  display: none;
  position: relative;
  left: calc(50vw - 50px);
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 15px;
  margin: 20px;
  display: flex;
}

.gallery .photo-card {
  width: 300px;
  border: 1px solid #d3d3d3;
